@import 'assets/styles/variable.sass';

.footer-wrap {
  width: 100%;
  height: 585px;
  background: #01111E;
  padding: 130px 0 90px;
  position: relative;
  min-width: 1440px;
  .footer {
    @include flex-box($JC: space-between);
    position: relative;
    height: 365px;
    .footer-title {
      width: 404px;
      font-size: 62px;
      line-height: 90px;
      color: #fff;
      margin-bottom: 35px; }
    .footer-list {
      @include flex-box($FD: column, $JC: space-between);
      .list-title {
        color: #B3B8BB;
        line-height: 28px;
        margin-bottom: 20px; }
      li {
        font-size: 18px;
        color: #fff;
        line-height: 40px; }
      .icon-list {
        height: 60px;
        @include flex-box($AI: center);
        a {
          margin-right: 32px;
          img {
            display: block; } } }
      &>div.list-title {
        line-height: 60px;
        margin-bottom: 0; } } } }

.is-phone {
  .footer-wrap {
    height: auto;
    padding: 0.5rem 0.25rem;
    min-width: 100%;
    .footer {
      display: block;
      height: auto;
      .footer-title {
        width: 90%;
        font-size: 48px;
        line-height: 60px;
        margin-bottom: 0.4rem; }
      .footer-list {
        display: block;
        margin-top: 0.4rem;
        .icon-list {
          height: auto;
          margin-top: 0.4rem; } } } } }
