@import './variable.sass';

@font-face {
  font-family: 'Gilroy-Regular';
  src: url('../fonts/Gilroy/Gilroy-Regular.ttf') format('truetype'); }
@font-face {
  font-family: 'Gilroy-Medium';
  src: url('../fonts/Gilroy/Gilroy-Medium.ttf') format('truetype'); }
@font-face {
  font-family: 'Gilroy-Bold';
  src: url('../fonts/Gilroy/Gilroy-Bold.ttf') format('truetype'); }

@font-face {
  font-family: 'GoogleSans-Regular';
  src: url('../fonts/Google-Sans-Font/GoogleSans-Regular.ttf') format('truetype'); }
@font-face {
  font-family: 'GoogleSans-Medium';
  src: url('../fonts/Google-Sans-Font/GoogleSans-Medium.ttf') format('truetype'); }
@font-face {
  font-family: 'GoogleSans-Bold';
  src: url('../fonts/Google-Sans-Font/GoogleSans-Bold.ttf') format('truetype'); }

body {
  color: $font-color;
  font-size: 16px;
  font-family: 'Gilroy-Medium', sans-serif;
  line-height: 24px;
  box-sizing: border-box;
  margin: 0 auto; }

.layout-1240 {
  width: 1240px;
  margin: 0 auto; }

.bold {
  font-weight: bold; }

.main {
  width: 100%;
  min-width: 1440px;
  margin-bottom: 100px;
  min-height: 600px; }

.grey {
  color: #656565; }

.content {
  width: 100%;
  min-height: calc(100% - 300px);
  background: white;
  position: relative; }

.logo-img {
  position: fixed;
  top: 40px;
  left: 40px;
  cursor: pointer;
  user-select: none;
  z-index: 3; }

.panel {
  padding: 100px 0; }

.is-phone {
  max-width: 750px;
  margin: 0 auto;
  .main {
    min-width: 100%; }
  .layout-1240 {
    width: auto; }
  .panel {
    padding: 0.48rem 0.25rem; }

  .container {
    width: 90%;
    margin: 0 auto;
    .common-title {
      .title {
        margin-bottom: 10px !important; } } } }

@import './intro.scss';
@import './projects.scss';
